import "./App.css";
import { useState, useEffect } from "react";
import { Button, Space, Descriptions } from "antd";

function App() {
  const [wordIdx, setWordIdx] = useState(0);
  const [wordCollapsed, setWordCollapsed] = useState(true);
  const [words, setWords] = useState([]);

  useEffect(() => {
    fetch("/words.json")
      .then((words) => words.json().then((words) => setWords(words)))
      .catch((error) => console.error(error))
  }, []);

  const nextWord = function (_) {
    setWordIdx((wordIdx + 1) % words.length);
    setWordCollapsed(true);
    return;
  };

  return (
    <div className="App">
      {words.length === 0 ? (
        <div>Loading Words...</div>
      ) : (
        <div className="WordContainer">
          <Descriptions title={words[wordIdx].text}>
            {!wordCollapsed ? (
              <Descriptions.Item label="">
                {words[wordIdx].translation}
              </Descriptions.Item>
            ) : (
              ""
            )}
          </Descriptions>
        </div>
      )}

      <div>
        <Space size="large">
          <Button
            type="primary"
            size="large"
            danger
            onClick={() => setWordCollapsed(false)}
          >
            Cannot Remember!
          </Button>
          <Button type="primary" size="large" onClick={nextWord}>
            Good
          </Button>
        </Space>
      </div>
    </div>
  );
}

export default App;
